lculate rem from\n  min-width: 100vw;\n  margin: 0;\n  padding: 0;\n  background-color: $page-bg;\n\n  //background: url('../images/capsule5.png');\n  //min-height: 100vh;\n  //background-repeat: no-repeat;\n  //background-attachment: scroll;\n  //background-attachment: scroll;\n  //background-position: center 0;\n  //background-size:  100%;\n\n  //background-blend-mode: normal;\n\n  @media (max-width: 768px) {\n    font-size: 14px; // Smaller base on mobile\n  }\n  //overflow-x: hidden; /* Prevent horizontal scrolling */\n\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n  font-size: clamp(2rem, 5vw, 4rem);\n  line-height: 1.0;\n  font-weight: 400;\n  font-family: $body-font;\n  color: $text-light;\n  max-width: 1200px;\n  margin: 25px auto;\n  padding: 0 20px;\n  letter-spacing: 0rem;\n  overflow-x: hidden; /* Prevent horizontal scrolling */\n\n}\n\n// Typography\nh1, h2, h3, h4, h5, h6 {\n  line-height: 1;\n  //text-rendering: optimizeLegibility;\n  //text-rendering: geometricPrecision;\n  text-rendering: optimizeSpeed;\n  font-smooth: never;\n  -webkit-font-smoothing: none;\n  margin-top: 0;\n  margin-bottom: 2rem;\n  font-weight: 300;\n  font-family: $header-font;\n  color: $text-light;\n}\n\nh1 { font-size: 6rem; line-height: 1.25; letter-spacing: 0rem; }\nh2 { font-size: 6rem; line-height: 1.3; letter-spacing: 0rem; }\nh3 { font-size: 5rem; line-height: 1.35; letter-spacing: 0rem; }\nh4 { font-size: 5rem; line-height: 1.5; letter-spacing: 0rem; }\nh5 { font-size: 3rem; line-height: 1.6; letter-spacing: 0rem; }\nh6 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\n\n//.pixel-perfect-text {\n//  font-size: 64px; // Base font size (usually pixel font's design size)\n//  // Use math.div() instead of / operator\n//  $base-width: 320px;\n//  $scale-factor: math.floor(math.div(100vw, $base-width));\n//  $scale-factor: math.max(1, $scale-factor);\n//\n//  transform-origin: left top;\n//  transform: scale($scale-factor);\n//}\n\n// Pixel-perfect text mixin\n@mixin pixel-text-base($size) {\n  font-size: $size + px;\n  $base-width: 320px;\n  $scale-factor: math.floor(math.div(100vw, $base-width));\n  $scale-factor: math.max(1, $scale-factor);\n\n  transform-origin: left top;\n  transform: scale($scale-factor);\n}\n\n// Generate pixel text classes with different sizes\n$pixel-text-sizes: 16, 32, 48, 64, 80, 96;\n\n@each $size in $pixel-text-sizes {\n  .pixel-text-#{$size} {\n    @include pixel-text-base($size);\n  }\n}\n\n\n// Marketing section styling\n.marketing-content {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n  background: $card-bg;\n  padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n}\n\n.tagline {\n  margin-bottom: 20px;\n  color: $accent-color;\n}\n\n.game-description {\n  margin: 20px 0;\n  max-width: 800px;\n}\n\n.cta-button {\n  margin-top: 20px;\n  padding: 10px 20px;\n  background-color: $accent-color;\n  color: #000;\n  border-radius: 4px;\n  display: inline-block;\n  transition: background-color 0.3s ease;\n\n  &:hover {\n    background-color: color.adjust($accent-color, $lightness: -20%);\n  }\n}\n\n// Links\na {\n  font-weight: normal;\n  text-decoration: none;\n  color: $accent-color;\n\n  &:focus {\n    outline: thin dotted;\n    color: color.adjust($accent-color, $lightness: 10%);\n  }\n}\n\nheader {\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  width: 100%;\n}\nfooter {\n  background: $card-bg;\n  padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n}\n\n// Adjust content container width for larger screens\nbody {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n\n  max-width: 2400px; // Increased to match gallery container\n  margin: 25px auto;\n  padding: 0 30px; // Increased padding for larger screens\n\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) {\n    max-width: 2400px;\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    max-width: 1400px;\n  }\n\n  @media (min-width: 769px) and (max-width: 1500px) {\n    max-width: 800px;\n  }\n\n  @media (max-width: 768px) {\n    max-width: 700px;\n    padding: 0 15px;\n  }\n}\n\n.cropped-image {\n  margin: 0px;\n  padding: 0px;\n  //margin-top: 0;       /* Ensure no top margin */\n  //padding-top: 0;      /* Ensure no top padding */\n  width: 100%; /* Container width */\n  height: 300px; /* Fixed height */\n  overflow: hidden; /* Hide parts outside container */\n  //position: relative;\n  overflow: hidden;\n\n}\n\n.cropped-image img {\n  /* Center the image horizontally */\n\n  position: absolute;\n  left: 50%;\n  transform: translateX(-50%);\n  /* Optional: adjust vertical position */\n  //top: 0;\n  max-width: none;     /* IMPORTANT: Prevents the image from resizing to fit container */\n\n  /* Maintain aspect ratio */\n  height: 100%;\n  width: auto;\n  z-index: -1; /* Place behind all other content */\n}\n\n\n/* Update the banner image class for pixel-perfect rendering */\n.banner-image {\n  /* Pixel-perfect rendering properties */\n  image-rendering: pixelated;\n  image-rendering: -moz-crisp-edges;\n  image-rendering: crisp-edges;\n  -ms-interpolation-mode: nearest-neighbor; /* IE */\n\n  /* Center the image */\n  display: block;\n  margin: 0 auto;\n\n  /* Prevent distortion */\n  max-width: 100%;\n  height: auto;\n\n  /* Maintain aspect ratio and original pixel grid */\n  transform-origin: center;\n\n  /* Original styles */\n  border-radius: $border-radius $border-radius 0 0;\n\n  /* Add a container around the image with a specific width if needed */\n  /* Example - adjust based on actual image dimensions */\n  //width: calc(min(800px, 100%));\n}\n\n.image-placeholder {\n  //margin-top: calc(100vh * 0.3552 + 47.6051px);\n  margin-bottom: 450px; /* More specific and forceful for testing */\n\n  @media (min-width: 2001px) { // 4 items per row for very large screens\n    margin-bottom: 650px;\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) { // 3 items per row\n    margin-bottom: 650px;\n  }\n\n  @media (min-width: 769px) and (max-width: 1500px) { // 2 items per row\n    margin-bottom: 400px;\n  }\n\n  @media (max-width: 768px) {\n    margin-bottom: 175px;\n  }\n\n}\n\n// Content sections\n.content {\n  background: $card-bg;\n  padding: 25px 25px 25px 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n#fullpage {\n  display: none;\n  position: fixed;\n  z-index: 9998;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background-color: rgba(0, 0, 0, 0.85); /* Add overlay background */\n  opacity: 0;\n  transition: opacity 0.3s ease-in-out;\n\n  .fullpage-image {\n    width: 90%; /* Reduced from 100% to create margin */\n    height: 90%; /* Reduced from 100% to create margin */\n    background-size: contain;\n    background-repeat: no-repeat;\n    background-position: center center;\n    background-color: transparent;\n    image-rendering: pixelated;\n    image-rendering: crisp-edges;\n    //transform: scale(1); /* Integer scaling for pixel art */\n    transform-origin: center center;\n    margin: 5vh auto; /* Add vertical and horizontal margin */\n  }\n}\n\n// Gallery styles\n.gallery-container {\n  width: 100%;\n  //max-width: 2400px; // Increased for large monitors\n  margin: 0 auto;\n}\n\n.artwork-grid {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 20px;\n  justify-content: center;\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) { // 4 items per row for very large screens\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(25% - 15px);\n      max-width: calc(25% - 15px);\n    }\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) { // 3 items per row\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(33.333% - 14px);\n      max-width: calc(33.333% - 14px);\n    }\n  }\n\n  @media (min-width: 764px) and (max-width: 1500px) { // 2 items per row\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(50% - 10px);\n      max-width: calc(50% - 10px);\n    }\n  }\n}\n\n.artwork-item {\n  border-radius: $border-radius;\n  box-shadow: 0 4px 12px color.adjust($card-bg, $alpha: -0.5);\n  transition: transform 0.3s ease, box-shadow 0.3s ease;\n  overflow: hidden;\n  margin-bottom: 20px;\n  position: relative;\n  display: inline-block;\n\n  img {\n    object-fit: contain;\n    width: 100%;\n    image-rendering: pixelated;\n    image-rendering: crisp-edges;\n    border-radius: $border-radius $border-radius 0 0;\n    display: block;\n    //@media (max-width: 768px) {\n    //  height: 300px;\n    //}\n  }\n\n  // Default (mobile) styling\n  @media (max-width: 768px) {\n    flex: 0 0 100%;\n    max-width: none;\n  }\n\n  &:hover {\n    transform: translateY(-5px);\n    box-shadow: 0 6px 16px color.adjust($card-bg, $alpha: -0.4);\n    background: color.adjust($card-bg, $alpha: 0);\n  }\n}\n\n.artwork-image-large {\n  object-fit: contain;\n  image-rendering: pixelated;\n  image-rendering: crisp-edges;\n\n  max-width: 100%;\n  border-radius: $border-radius $border-radius 0 0;\n}\n\n\n.artwork-info {\n  position: relative;\n  background-color: #3c3f41;\n  font-size: 2.4rem;\n  color: #fff;\n  padding: 10px 20px;\n  text-align: center;\n\n  .artwork-title {\n    margin: 0 0 15px 0;\n    color: $text-light;\n    color: color.adjust($text-light, $alpha: -0.15);\n  }\n\n  .artwork-description {\n    display: none;\n    color: color.adjust($text-light, $alpha: -0.15);\n  }\n}\n\n.icon-image {\n  height: 100px;\n  width: 100%;\n  object-fit: fill;\n  border-radius: $border-radius $border-radius 0 0;\n  filter: invert(28%) sepia(79%) saturate(2476%) hue-rotate(56deg) brightness(118%) contrast(119%);\n}\n\n.icon-item {\n  .icon-info {\n    padding: 10px;\n    margin-right: 10px;\n\n    .icon-title {\n      font-size: 2.4rem;\n      margin: 0 0 10px 0;\n      color: $text-light;\n    }\n\n    .icon-description {\n      font-size: 2.4rem;\n      color: color.adjust($text-light, $alpha: -0.15);\n      line-height: 1.2;\n      text-align: center;\n    }\n  }\n}\n\n\n// Blockquote styles\nblockquote {\n  border-left: 2px solid color.adjust($accent-color, $saturation: 10%);\n  margin: 1.6em 0;\n  padding: 1.5em;\n  font-size: 1.8rem;\n  background: color.adjust($card-bg, $alpha: -0.4);\n  border-radius: $border-radius;\n\n  p:last-child {\n    margin-bottom: 0;\n  }\n}\n\n\n"],"sourceRoot":""}]);
